
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";
import XInput from 'vux/src/components/x-input/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'

import Calendar from 'vux/src/components/calendar/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import ScrollList from '@/components/common/ScrollList'
import banner from '@/assets/img/banner.png'
import change from '@/assets/img/change.png'
export default {
name: "Ticket",
components: {
AlertModule,
ScrollList,
XInput,
XSwitch,
Checklist,
Popup,
Group,
Cell,
Toast,
XAddress,
XButton
},
data() {
return {
commitStatus: false,

phone:'',
canBuyTicketLaterStatus: false,
checkSeatList: [],
seatStatus: false,
commonList: [],
checklist003: [],
show13: false,
demo5: [],
change,
banner,
title: "首页",
current: 0,
param: {
origin: "",
destination: "",
expectDate: "",
trainNumber: "",
seat: "",
passengerIdList: [],
phone: "",
canBuyTicketLater: "N"
}
};
},
computed: {
...mapGetters("CONTAINER", [
"origin",
"destination",
"specifiedDate",
"seatType",
"passengerIdListName",
"trainNumberStr",
"selectDayList",
"passengerIdList",
"currentSeatTypeArr",
"trainNumberList"
])
},
mounted() {
this.getDaysList();
},
methods: {
...mapMutations("CONTAINER", ["setOptions"]),

getDates(days, todate) {
var dateArry = [];
var index = 0;
if (days < 0) {
index = days;
days = 0;
}
for (var i = index; i <= days; i++) {
var dateObj = this.dateLater(todate, i);
dateArry.push(dateObj);
}
return dateArry;
},
dateLater(dates, later) {
let dateObj = {};
let show_day = new Array(
"周日",
"周一",
"周二",
"周三",
"周四",
"周五",
"周六"
);
let date = new Date(dates.replace(/-/g, "/"));
date.setDate(date.getDate() + later);
let day = date.getDay();
let yearDate = date.getFullYear();
let month =
date.getMonth() + 1 < 10
? "0" + (date.getMonth() + 1)
: date.getMonth() + 1;
let dayFormate =
date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
dateObj.time = yearDate + "-" + month + "-" + dayFormate;
dateObj.week = show_day[day];
return dateObj;
},
getDaysList() {
let date = new Date();
let nextDays = this.getDates(
40,
date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate()
);

let trainDateList = [];
for (let i in nextDays) {
let temp = nextDays[i].time.split("-");
let day = temp[0] + "年" + temp[1] + "月" + temp[2] + "日";
trainDateList.push({
key: nextDays[i].time,
value: day + nextDays[i].week
});
}
this.commonList = trainDateList;
console.log(this.commonList)
this.setOptions({
todayTime: this.commonList[0].key
});
},

chooseDays() {
var chooseDays = this.chooseDayArr.join("、");
this.setOptions({
specifiedDate: chooseDays,
selectDayList: this.selectDayList1
});
this.show13 = false;
},


onClick(newVal, oldVal) {
this.canBuyTicketLaterStatus = newVal;
},


saveOrder() {
this.commitStatus = true;
if(this.selectDayList.length == 0){
AlertModule.show({
title: '提示',
content: '指定日期不能为空'
})
this.commitStatus = false;
return
}else if (this.trainNumberList.length == 0) {
AlertModule.show({
title: '提示',
content: '车次不能为空'
})
this.commitStatus = false;
return
}else if(this.passengerIdList.length == 0) {
AlertModule.show({
title: '提示',
content: '乘车人不能为空'
})
this.commitStatus = false;
return
} else if(this.phone == '') {
AlertModule.show({
title: '提示',
content: '号码不能为空'
})
this.commitStatus = false;
return
}
this.$vux.loading.show({
text: "请稍候……"
});
this.param = {
origin: this.origin,
destination: this.destination,
expectDate: this.specifiedDate,
trainNumber: this.trainNumberStr,
seat: this.seatType,
passengerIdList: this.passengerIdList,
phone: this.phone,
canBuyTicketLater: this.canBuyTicketLater? 'Y' : 'N'
};
this.$store.dispatch("ORDER/saveOrder", this.param).then(res => {
this.$vux.loading.hide()
console.log(res);
AlertModule.show({
title: '提交成功',
content: '请告诉抢票人员你已填写完毕吧',
onShow () {
this.commitStatus = false;
}
})
});
},

chooseSeat() {
var chooseSeats = this.chooseSeatArr.join("、");
this.setOptions({
seatType: chooseSeats
});
this.seatStatus = false;
},
changeSeatCheck(val, label) {
this.checkSeatList = val;
this.chooseSeatArr = val;
},
changeCheck(val, label) {
this.selectDayList1 = val;
this.chooseDayArr = val;
},
onChange(val) {
console.log("on change", val);
},
changeStation() {
var station = this.origin;
this.setOptions({
origin: this.destination,
destination: station
});


},
goDetail() {
this.$router.push({ name: "detail" });
},
searchList() {
if(this.selectDayList.length == 0){
AlertModule.show({
title: '提示',
content: '请选择指定日期',
onShow () {
return
}
})
}else{
this.$router.push("/ticketList");
}
},
chooseCity(curType) {

this.$router.push({ path: "cityList", query: { type: curType } });
},
riderList() {
this.$router.push("/riderList");
}
}
};



